@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.125em;
    font-weight: 300;
    border-radius: .625em;
    width: 16.375em;
    max-width: 100%;
    position: relative;
    span{
        font-size: 1.25em;
    }
    &:focus {
        box-shadow: none;
    }
    svg,
    img{
        top: .875em;
        right: .64em;
        position: absolute;
        width: .8125em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: $secondary;
        color: $white;
    }
    &:focus{
        box-shadow: inset 0px 0px 6px 0px rgba(0, 0, 0, 0.25);
    }
    &:active{
        box-shadow: inset 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    }
    &._disabled,
    &:disabled{
        background-color: $gray300;
        pointer-events: none;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        border-color: $primary;
        background-color: $primary;
        color: $white;
        path{
            stroke: $white;
        }
    }
    &:focus{
        box-shadow: inset 0px 0px 6px 0px rgba(0, 0, 0, 0.25);
    }
    &:active{
        box-shadow: inset 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    }
    &._disabled,
    &:disabled{
        border-color: $gray300;
        background-color: $gray300;
        pointer-events: none;
        color: $white;
        path{
            stroke: $white;
        }
    }
}
