.why-work{
  margin-top: 7.625em;
  display: grid;
  grid-template-columns: 25.1875em calc(100% - 25.1875em - 4.5em);
  gap: 1em 4.5em;
  align-items: center;
}

.why-slider{
  padding: 1em 0;
  width: calc(100% + (100vw - 1280px) / 2 + 5.125em);
  .swiper{
    &-container{
      width: 15em;
    }
    &-slide{
      padding-right: 1em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &-active~.swiper-slide,
      &-active{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.why-card{
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.50);
  height: 10.8125em;
  border-radius: 1em;
  background-color: $secondary;
  color: $white;
  padding: 1.625em 1.25em;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  &--primary{
    background-color: $primary;
  }
  &--gray{
    background-color: $gray800;
  }
  &--gray2{
    background-color: $gray300;
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 600;
  }
  &__num{
    border: 2px solid $white;
    @include center;
    width: 1.875em;
    height: 1.875em;
    font-weight: 700;
    font-family: $font3;
    border-radius: 50%;
    margin-left: auto;
  }
}

.docs{
  margin-top: 9.38em;
  &__grid{
    margin-top: 2.75em;
    max-width: 52em;
  }
}

.doc-card{
  box-shadow: $shadow;
  border-radius: 1em;
  color: $white;
  background-color: $primary;
  padding: 2.78em 1.5em 1.84em;
  min-height: 18em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &__bottom{
    padding-top: 1em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__icon{
    width: 3em;
    display: block;
    margin-bottom: .31em;
  }
  &__download{
    width: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .why-work{
    margin-top: 3em;
    grid-template-columns: 100%;
    position: relative;
    padding-bottom: 1.25em;
    overflow: hidden;
    .swiper-scrollbar{
      position: absolute;
      bottom: 0;
      left: 1em;
      width: calc(100% - 2em);
    }
    @media screen and (min-width: 577px){
      width: calc(100% + 12.375em);
      margin-left: -11.375em;
      padding-left: 11.375em;
      .swiper-scrollbar{
        left: 11.375em;
        width: calc(100% - 12.375em);
      }
    }
  }

  .why-slider{
    padding-top: 0;
    width: 100%;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        opacity: 1;
        pointer-events: unset;
        width: 15em;
      }
    }
  }

  .docs{
    margin-top: 3em;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .why-work{
    width: calc(100% + 2em);
    margin-left: -1em;
    padding-left: 1em;
  }

  .docs{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
