.footer{
  background-image: url(../img/footer-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 3.5em 0 3em;
  color: $white;
  &__inner{
    display: flex;
    justify-content: space-between;
    padding-right: 5.25em;
  }
  &__col4{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__logo{
    display: block;
    width: 6em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    margin-top: .67em;
    display: inline-block;
    &:first-child{
      margin-top: 0;
    }
  }
  &__links{
    margin-top: .44em;
    @include grid100gap(.31em);
    a{
      font-size: .8125em;
      line-height: 120%;
    }
  }
  &__scroll-top{
    width: 2.5em;
    opacity: .5;
    &:hover{
      opacity: 1;
      rect{
        fill: $secondary;
        stroke: $secondary;
      }
    }
    svg{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 1.25em;
  }
  &__socials{
    display: flex;
    align-items: center;
    &-btn{
      width: 1.5625em;
      flex-shrink: 0;
      margin-right: .5em;
      &:hover{
        path{
          fill: $primary;
        }
      }
      svg{
        width: 100%;
        display: block;
      }
    }
  }
  &__c{
    font-size: .6875em;
    line-height: 120%;
    margin-top: 1.2em;
    color: $gray300;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2em 0;
    &__inner{
      padding-right: 0;
    }
    &__title{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      @include grid100gap(1.5em);
    }
    &__scroll-top{
      margin-left: auto;
    }
    &__bottom{
      margin-top: -2.5em;
    }
  }
}
