.about{
  padding: 3.44em 0 10.5em;
  &__grid{
    display: grid;
    grid-template-columns: 9.375em calc(100% - 10.375em);
    gap: 1em;
  }
}

.about-nav{
  position: sticky;
  top: 1em;
  font-weight: 300;
  color: #343434;
  ul{
    @include grid100gap(1.25em);
  }
  a{
    font-size: 1em;
    line-height: 120%;
  }
  ._active{
    font-weight: 600;
    color: $primary;
  }
}

.about-company{
  &__top{
    display: grid;
    justify-content: space-between;
    align-items: center;
    grid-template-columns: 25.25em 29.9375em;
  }
  &__desc{
    margin-top: 1em;
  }
  &__hint{
    padding: 1.18em;
    border-left: 1px solid #C1C1C1;
  }
  &__img{
    width: calc(100% + 5.125em);
    margin-top: 3.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.company-history{
  margin-top: 8.5em;
}

.history-slider{
  margin-top: 3.75em;
  overflow: hidden;
  &__pagination{
    margin-top: 2.69em;
    height: 2.5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-btn{
      font-size: 1.25em;
      font-weight: 300;
      line-height: 120%;
      cursor: pointer;
      &._active{
        color: $primary;
        font-size: 2.5em;
        font-weight: 600;
      }
      &:hover{
        color: $primary;
      }
    }
  }
}

.history-card{
  display: grid;
  grid-template-columns: 23.6875em calc(100% - 23.6875em - 1.5em);
  gap: 1.5em;
  &__img{
    @include imgCover(94.7%);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.50);
  }
  &__col{
    padding-top: .94em;
  }
  &__text{
    margin-top: .94em;
  }
}

.swiper-scrollbar{
  height: 1.0625em;
  border-radius: .625em;
  background-color: $gray300;
  position: static;
  margin-top: .625em;
  &-drag{
    border-radius: .625em;
    background: $primary;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 1.5em 0 3em;
  }

  .about-company{
    &__top{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__hint{
      padding: 1em 0 0;
      border-left: none;
      border-top: 1px solid #C1C1C1;
    }
    &__img{
      width: calc(100% + 1em);
      margin-top: 1.5em;
    }
  }

  .company-history{
    margin-top: 3em;
  }

  .history-slider{
    margin-top: 1.5em;
    &__pagination{
      display: none;
    }
  }

  .history-card{
    grid-template-columns: 100%;
    &__col{
      padding: 0;
      max-width: 25em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
  }
}
