.font2{
  font-family: $font2;
}

.gradient-text{
  background:  linear-gradient(90deg, #006EB7 0%, #000 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.h0{
  font-size: 3em;
  font-weight: 700;
  line-height: 120%;
  text-transform: uppercase;
}

.h1{
  font-size: 2.5em;
  text-transform: uppercase;
  font-weight: 600;
}

.desc1{
  font-weight: 500;
}

.desc1,
.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.desc3,
.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.cart-name{
  font-size: 1em;
  line-height: 120%;
  font-weight: 600;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.caps-text{
  text-transform: uppercase;
}

.primary-text{
  color: $primary;
}

.gray-text300{
  color: $gray300;
}

.gray-text800{
  color: $gray800;
}


@media screen and (max-width: 992px){
  .h0{
    font-size: 2em;
  }

  .h1{
    font-size: 1.75em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .desc1,
  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .desc3,
  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}
