@import "../vars.scss";
.page-top{
    color: $white;
    padding: 1.12em 0;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.40) 0%, rgba(0, 0, 0, 0.40) 100%), #006EB7;
}

.breadcrumbs {
    li{
        display: inline;
        &:not(:first-child) {
            margin-left: 2.19em;
        }
        &:last-child{
            font-weight: 600;
            pointer-events: none;
        }
    }
    a{
        font-size: .875em;
        line-height: 120%;
        &:hover{
            color: $white;
            opacity: .8;
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: 1em 0;
    }

    .breadcrumbs {
        li{
            &:not(:first-child) {
                margin-left: 1.5em;
            }
        }
    }

}


